




















































































































































































































































import Vue from 'vue';
import {mapState} from 'vuex';
import { mask } from 'vue-the-mask';
import Oa from "@/models/Oa";
import UnitPlan from "@/models/UnitPlan";
import UnitPlanOa from "@/models/UnitPlanOa";

function requestDefaultValues() {
    return {
        school_id: null,
        name: 'Unidad 1',
        name2: '',
        hours: '00',
        subject: null,
        schoolLevel: null,
        level: null,
        course: null,
        year: new Date().getFullYear(),
        startDate: new Date().toISOString().substr(0, 10),
        endDate: new Date().toISOString().substr(0, 10),
        oas: [],
    };
}

export default Vue.extend({
    name: 'RomaUnitModalForm',
    updated() {
        // this.request = requestDefaultValues();
        // si va a editar
        /*if (this.unit) {
            this.request = this.unit;
        }*/

        // console.log(this.request.id)
    },
    props: {
        value: Boolean,
        unit: Object,
        school: {
            type: Object,
            required: true,
        },
        maxWidth: {
            type: String,
            default: '800',
        },
        overlayOpacity: {
            type: String,
            default: '0.8',
        },
        persistent: {
            type: Boolean,
            default: true,
        },
    },
    data() {
        return {
            request: requestDefaultValues(),
            selectedUnit: null,
            valid: false,
            loading: false,
            showing: this.value,
            startDateMenu: false,
            endDateMenu: false,
            courses: ['A', 'B', 'C', 'D', 'E', 'F'],
        };
    },
    computed: {
      units () {
        return UnitPlan.all();
      },
      unitNames () {
        let unitNames = [];

        for (let n = 1; n <= 7; n++) {
            unitNames.push('Unidad ' + n)
        }

        return unitNames;
      },
      ...mapState([
          'roma',
      ]),
    },
    methods: {
        show(value) {
            this.$emit('input', value);
            this.request = requestDefaultValues();
            this.selectedUnit = null;
        },
        setUnit() {
            this.request.school_id = this.school.id;
            if (this.request.id) delete this.request.oas;

            let _request = this.request;
            UnitPlan.insertOrUpdate({data: this.request}).then(data => {

                if (!_request.id) { // Si edita no vuelvo a agregar los oas
                    let oas = Oa.all(); // Obtengo todos los oas con sus ies

                    oas = oas.map(oa => {
                        return {
                            unitplan_id: data.unit_plans[0].id,
                            oa_id: oa.id
                        }
                    });

                    UnitPlanOa.insert({data: oas});
                }
            });
            this.show(false);
            this.request = requestDefaultValues();
        },
        cloneUnit () {
            // this.request.name = this.selectedUnit.name;
            this.request.name2 = this.selectedUnit.name2;
            this.request.subject = this.selectedUnit.subject;
            this.request.schoolLevel = this.selectedUnit.schoolLevel;
            this.request.level = this.selectedUnit.level;
            this.request.course = this.selectedUnit.course;
            this.request.year = this.selectedUnit.year;
            this.request.startDate = this.selectedUnit.startDate;
            this.request.endDate = this.selectedUnit.endDate;
        },
    },
    directives: {
        mask
    }
});
