












































































































import Vue from 'vue';
import { createSimpleTransition } from 'vuetify/src/util/helpers';
import RomaUnit from './RomaUnit.vue';
import RomaUnitModalForm from './RomaUnitModalForm.vue';
import RomaConfirmDialog from '@/components/partial/RomaConfirmDialog.vue';
import UnitPlan from "@/models/UnitPlan";
import UnitPlanOa from '@/models/UnitPlanOa';
import UnitPlanActivity from '@/models/UnitPlanActivity';
import UnitPlanAttitude from '@/models/UnitPlanAttitude';

const romaUnitListTransition = createSimpleTransition('unit-list-transition');

Vue.component('my-transition', romaUnitListTransition);

export default Vue.extend({
    name: 'RomaUnitList',
    props: {
        school: {
            type: Object,
            required: true,
        },
    },
    data() {
        return {
            dialogDeleteUnit: false,
            selectedUnit: null,
            showUnitModalForm: false,
        };
    },
    methods: {
        editUnit(unit) {
            this.$refs.unitModalForm.request = unit;
            this.showUnitModalForm = true;
        },
        goDelUnit(unit: object) {
          this.dialogDeleteUnit = true;
          this.selectUnit(unit);
        },
        delUnit() {
            UnitPlan.delete(this.selectedUnit.id);
            UnitPlanOa.delete(detail => detail.unitplan_id === this.selectedUnit.id); // Borro OAs
            UnitPlanActivity.delete(detail => detail.unitplan_id === this.selectedUnit.id); // Actividades
            UnitPlanAttitude.delete(detail => detail.unitplan_id === this.selectedUnit.id); // Actitudes
            this.dialogDeleteUnit = false;
        },
        selectUnit(unit) {
          this.selectedUnit = unit
        },
        /*goUnitDetails(unit) {
            /!*this.selectUnit(unit);
            this.$store.commit('selectUnit', unit);*!/
            this.$router.push({
                name: 'organizer-unit',
                params: {id: unit.id},
            });
        }*/
    },
    components: {
        RomaUnit,
        RomaConfirmDialog,
        RomaUnitModalForm,
    },
});
