





















import Vue from 'vue';

// Roma Components
import RomaOrganizer from '@/components/RomaOrganizer.vue';
import RomaUnitList from '@/components/RomaUnitList.vue';
import School from '@/models/School';

export default Vue.extend({
    name: 'OrganizerPage',
    props: {},
    data() {
        return {};
    },
    computed: {
        school () {
            const index = this.$store.state.organizer.selectedSchool.id;
            return School.query().
                          whereId(index).
                          with('unitplans', query => {
                              query.with('coverage')
                          }).
                          first();
        }
    },
    beforeRouteEnter (to, from, next) {
        next(vm => {
            if (vm.school) {
                return true;
            } else {
                return {name: 'home'};
            }

        });

    },
    components: {
        RomaOrganizer,
        RomaUnitList,
    },
});
